import React from 'react';
import Loader from '../../components/loader/Loader';
import ProjectStyles from './Project.style';

const Project = (props) => {
    const {
        theme,
        data
    } = props;

    if(!data) {
        return null;
    }

    return (
        <ProjectStyles>
            <div className="container-slides">
                <div className="wrap-iframe d-flex align-items-center pos-relative">
                    <iframe src={data.link_docs.project} frameBorder="0" width="100%" height="100%" allowFullScreen={true}></iframe>
                    <Loader color={theme.primary} size={8} />
                </div>
            </div>
        </ProjectStyles>
    )
}

export default Project;